import React, { Component } from 'react';
import biscoito from './assets/biscoito.png';
import './estilo.css';

class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            textoFrase: ''
        };
        
        

        this.quebraBiscoito = this.quebraBiscoito.bind(this);

        this.frases = ['Siga os bons e aprenda com eles.',
                       'O bom-senso vale mais do que muita coisa.',
                       'O riso é a menor distância entre duas pessoas.',
                       'Deixe de lado as preocupações e seja feliz.',
                       'Realize o óbvio, pense no improvável e conquiste o impossível.',
                       'Acredite em milagres, mas não dependa deles.',
                       'A maior barreira para o sucesso é o medo do fracasso.',
                       'A vida trará coisas boas se tiver paciência.',
                       'Não compense na ira o que lhe falta na razão.',
                       'Defeitos e virtudes são apenas dois lados da mesma moeda.',
                       'A maior de todas as torres começa no solo.',
                       'Há três coisas que jamais voltam; a flecha lançada, a palavra dita e a oportunidade perdida.',
                       'A juventude não é uma época da vida, é um estado de espírito.',
                       'Não importa o tamanho da montanha, ela não pode tapar o sol.',
                       'A sorte favorece a mente bem preparada.',
                       'Espere pelo mais sábio dos conselhos: o tempo.',
                       'Todas as coisas são difíceis antes de se tornarem fáceis.',
                       'Você é do tamanho do seu sonho',
                       'Todo mundo é capaz de denominar uma dor, exceto quem a sente.',
                       'O amor está mais próximo do que você imagina.',

                    ]
    
    }

    quebraBiscoito() {
        let state = this.state;
        let numeroAleatorio = Math.floor(Math.random() * this.frases.length);
        state.textoFrase = ' "' + this.frases[numeroAleatorio] + '" ';
        this.setState(state);

    }


    render() {
        return (
            <div className="container">
                <img src={biscoito} className="img" />
                <div className="btn_div">
                <Botao nome="Abrir" acaoBtn={this.quebraBiscoito} />
                </div>
                <div className="textoFrase">
                    <h3 >{this.state.textoFrase}</h3>
                </div>
                
            </div>
        )
    }
}

class Botao extends Component {
    render() {
        return (
            <div>
                <button className="button" onClick={this.props.acaoBtn} > {this.props.nome} </button>
            </div>
        )
    }

}

export default App;

